.loginPage {
    align-items: flex-start;
    background-color: var(--white);
    display: flex;
    overflow: hidden;
    width: 100%;
    min-height: 100vh;
    box-sizing: border-box;
  }
  
  
  .pageContainer {
    width: 100%;
    min-height: 100vh;
    /* height: 1080px; */
    margin-left: -42px;
    position: relative;
  }
  
  .pageBackground {
    background-color: #16181b;
    /* height: 1080px; */
    min-height: 100vh;
    left: 42px;
    position: absolute;
    top: 0;
    /* width: 1920px; */
    width: 100%;
  }
  
  .backgroundImage{
    /* height: 1080px; */
    min-height: 100vh;
    left: 42px;
    object-fit: cover;
    position: absolute;
    top: 0;
    /* width: 1920px; */
    width: 100%;
  }
  
  .logo {
    height: 152px;
    left: 0;
    object-fit: cover;
    position: absolute;
    top: 22px;
    width: 409px;
  }
  
  .loginComponents {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    left: 620px;
    min-height: 560px;
    position: absolute;
    top: 221px;
    width: 660px;
  }
  
  .passRecoveryText {
    color: var(--cardinal);
    font-family: var(--font-family-overpass);
    font-size: 36px;
    font-weight: 800;
    letter-spacing: 3.60px;
    min-height: 5px;
  }
  
  .regContainer{
    align-items: flex-start;
    display: flex;
    height: 33px;
    margin-top: 20px;
    min-width: 403px;
  }
  
  .text2 {
    color: var(--white);
    font-family: var(--font-family-nunito);
    font-size: var(--font-size-l);
    letter-spacing: 2.40px;
    min-height: 33px;
    min-width: 279px;
  }
  
  .text3Container {
    color: transparent;
    cursor: pointer;
    font-family: var(--font-family-nunito);
    font-size: var(--font-size-l);
    letter-spacing: 2.40px;
    margin-left: 6px;
    min-height: 33px;
  }
  
  
  .text3{
    color: #b62b2b;
    font-weight: 700;
    letter-spacing: 0.58px;
    text-decoration:underline;
    text-decoration-color: var(--cardinal);
  }
  
  
  .passRecoveryEmail{
    align-items: center;
    align-self: center;
    display: flex;
    flex-direction: column;
    margin-left: 8.0px;
    margin-top: 0px;
    min-height: 0px;
    width: 640px;
  }
  
  .loginErrorMessage{
    margin-top: 140px;
    margin-left: 20px;
    position: absolute;
    color: var(--cardinal);
  }
  
  .passRecoveryEmailContainer {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    min-height: 132px;
    width: 640px;
    margin-top: 50px;
  }
  
  .emailUsername,
  .password-2 {
    letter-spacing: 2.80px;
  }
  
