    height: 78px;
    margin-top: 16px;
    min-width: 640px;
    padding: 24px 32px;
  } */
  
  .enterEmailUsername {
    align-items: flex-start;
    background-color: var(--white);
    border-radius: 80px;
    display: flex;
    height: 78px;
    margin-top: 16px;
    min-width: 640px;
    padding: 24px 32px;
    border: 0px;
  }
  
  .enterEmailUsername:focus,
  .enterPassword:focus
  {
  box-shadow: 0px 5px 10px  var(--cardinal);
  }
  
  .enterEmailUsername::placeholder,
  .enterPassword::placeholder {
    color:  gray;
  }
  
  .loginPasswordContainer {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    min-height: 134px;
    width: 640px;
    border:0px;
  }
  
  
  .enterPassword{
    align-items: flex-start;
    background-color: var(--white);
    border-radius: 80px;
    display: flex;
    height: 78px;
    margin-top: 16px;
    min-width: 640px;
    padding: 24px 32px;
    border: 0px;
  }
  
  .buttonContainer{
    cursor: pointer;
    display: flex;
    margin-left: 2.0px;
    margin-top: 65px;
    min-width: 445px;
    transition: all 0.2s ease;
  }
  
  .buttonContainer:hover {
    transform: translate(0, -5px);
  }
  
  .loginButton {
    background-color: var(--cardinal);
    border-radius: 10px;
    display: flex;
    height: 61px;
    min-width: 443px;
    padding: 10px 180px;
    border-width: 0px;
  }
  
  .loginButton:disabled{
    background-color: var(--cardinal);
    opacity: 40%;
    cursor: not-allowed;
  }
  
  .resendEmailText {
    /* margin-top: 140px; */
    /* margin-left: 20px;
    position: absolute; */
    color: var(--white);
    margin-left: -130px;
    margin-top: 10px;
    color: var(--white);
    font-family: var(--font-family-nunito);
    font-size: var(--font-size-l);
    letter-spacing: 2.40px;
    min-height: 33px;
    min-width: 279px;
  }
  
  .resendText{
    margin-top: 10px;
    color: #b62b2b;
    font-weight: 700;
    letter-spacing: 0.58px;
    text-decoration:underline;
    text-decoration-color: var(--cardinal);
  }

  .emailUsername:after,
  .password-2:after {
    content:" *";
    color: var(--cardinal);
  }
  

  
  